  }

  .brands-grid__list {
    margin-bottom: 1.5625rem;
  }

  .brands-grid__item {
    flex-basis: 24%;
  }

  .brands__item {
    flex: 0 0 100%;
    margin-right: 0;
  }

  .brands__title {
    font-size: 3rem;
  }

  .brands__desc {
    font-size: 1.2rem;
  }

  .breadcrumbs__item::after {
    margin: 0 5px;
  }

  .footer__col:nth-child(2) {
    display: none;
  }

  .logo__icon {
    width: 11.25rem;
    height: 2.714285714285714rem;
  }

  .nav__item {
    margin-right: .5714285714285714rem;
  }

  .nav__link {
    font-size: .8571428571428571rem;
  }

  .news__list {
    flex-direction: column;
  }

  .news__item {
    flex-direction: row;
    padding: 0;
    margin-bottom: 1.5625rem;
  }

  .news__title {
    font-size: 1.2857142857142866rem;
  }

  .news__img {
    max-width: 21.42857142857143rem;
    margin-bottom: 0;
    margin-right: 1.5625rem;
  }

  .partners__pic {
    flex-basis: 33%;
    padding: 1rem;
    margin-right: 1rem;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .main {
    margin: 0;
  }

  .brands-grid__item {
    flex-basis: 50%;
  }

  .brands {
    margin: 0;
  }

  .copyright {
    margin-top: 5px;
    margin-bottom: 1.5625rem;
  }

  .footer {
    padding: 0.78125rem 0;
  }

  .footer__container {
    flex-direction: column;
  }

  .footer__col:nth-child(4),
  .footer__col:nth-child(3) {
    display: none;
  }

  .footer__title {
    display: none;
  }

  .logo {
    margin: auto;
  }

  .mobile-menu {
    display: flex;
  }

  .nav {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.85);
    z-index: 10;
  }

  .nav__list {
    flex-direction: column;
    text-align: center;
  }

  .nav__item {
    margin-right: 0;
    margin-bottom: 1.5625rem;
  }

  .nav__link {
    font-size: 2rem;
  }

  .news__item {
    flex-direction: column;
  }

  .news__img {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 1.5625rem;
  }

  .page-header {
    display: none;
  }

  .partners__item {
    flex-direction: column;
  }

  .partners__pic {
    max-width: 50%;
    margin-right: 0;
    margin-bottom: 1rem;
  }

  .partners__desc {
    flex-basis: 100%;
  }

  .partners__title {
    font-size: 1.2rem;
  }

  .socials {
    display: none;
  }
